import { NetworkServiceProvider } from './../../providers/network-service/network-service';
import { Component, ViewChild } from '@angular/core';
import { IonicPage, NavController, NavParams, ToastController, MenuController, Content, Scroll } from 'ionic-angular';
import { BasePage } from '../base/base';
import { AppServiceProvider, AppGlobal } from '../../providers/app-service/app-service';

/**
 * Generated class for the AutomaticMonitorPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */
let automaticThis;
declare const $;
@IonicPage()
@Component({
  selector: 'page-automatic-monitor',
  templateUrl: 'automatic-monitor.html',
})
export class AutomaticMonitorPage extends BasePage {

  @ViewChild("automaticContent") automaticContent: Content;
  @ViewChild("headerAutomatic") headerAutomatic: Scroll;
  @ViewChild("leftAutomatic") leftAutomatic: Scroll;
  @ViewChild("rightAutomatic") rightAutomatic: Scroll;
  arrDate: string = "minute";
  minuteDate: minuteDate = new minuteDate;
  trees: any;
  treeId: any = '';
  AutomaticData: any = [];
  pageNum: any = 0;
  pageSize: any = 30;
  dataType: any = '1';
  style: style = new style;
  displayFormat: any;
  constructor(public navCtrl: NavController,
    public navParams: NavParams,
    public toastCtrl: ToastController,
    public menuCtrl: MenuController,
    public net: NetworkServiceProvider) {
    super(navCtrl, navParams, toastCtrl)
    automaticThis = this;

  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad AutomaticMonitorPage');
    this.time();
    this.scroll();
    this.getAutomaticData();
    this.getSiteTree();
  }
  query() {
    this.menuCtrl.enable(true, "automaticMenu");
    this.menuCtrl.toggle("automaticMenu");
  }
  monitor(item){
    item.siteName=item.stationName;
    item.siteType='1';
    this.navCtrl.push('AutomaticDetailsPage', { site: item, id: '1' });

  }
  determine() {
    this.AutomaticData = [];
    this.getAutomaticData();
    this.menuCtrl.enable(true, "automaticMenu");
    this.menuCtrl.toggle("automaticMenu");
  }
  time() {
    if (this.arrDate == "minute") {
      this.displayFormat = "YYYY-MM-DD hh:mm";
      this.minuteDate.startDate = new Date(new Date().getTime() + 7 * 60 * 60 * 1000).toISOString();
      this.minuteDate.endDate = new Date(new Date().getTime() + 8 * 60 * 60 * 1000).toISOString();
    } else if (this.arrDate == "hour") {
      this.displayFormat = "YYYY-MM-DD hh";
      this.minuteDate.startDate = new Date(new Date().getTime() + 5 * 60 * 60 * 1000).toISOString();
      this.minuteDate.endDate = new Date(new Date().getTime() + 8 * 60 * 60 * 1000).toISOString();
    } else if (this.arrDate == "day") {
      this.displayFormat = "YYYY-MM-DD";
      this.minuteDate.startDate = new Date(new Date().getTime() - 4 * 16 * 60 * 60 * 1000).toISOString();
      this.minuteDate.endDate = new Date(new Date().getTime() + 8 * 60 * 60 * 1000).toISOString();
    }
  }
  getSiteTree() {
    let param: any = {};
    param.userId = AppServiceProvider.getInstance().userInfo.userId;
    param.code = AppServiceProvider.getInstance().userInfo.code;
    this.net.httpPost(
      AppGlobal.API.getSiteTree,
      param, res => {
        console.log(res);
        this.trees = res;
        this.tree();
      }, error => {
        this.toast(error);
      }
    )
  }
  tree() {
    console.log(this.trees["水质站"])
    $('#jsTreeAutomatic').jstree({
      'core': {
        "themes": {
          "dots": false // no connecting dots between dots
        },
        'data': this.trees["水质站"]
      },
      "plugins": ["search"]
    });
    $('#jsTreeAutomatic').jstree().open_all();
    $('#jsTreeAutomatic').on("changed.jstree", (event, data) => {
      var str = data.node.id;

      if (str.slice(0, 1) != "-") {
        this.treeId = str;
      } else {
        this.treeId = '';
      }
      console.log(this.treeId)
    });
  }

  scrollStyle() {
    let automaticContent = $("#automaticContent").height();
    let automaticHeader = $("#automaticHeader").height();
    let automaticHead = $("#automaticHead").height();
    let automaticStationNames = $("#automaticStationNames").height();
    let automaticHeight = automaticContent - automaticHeader - automaticHead - 30;
    if (automaticStationNames < automaticHeight) {
      this.style.automaticHeight = automaticStationNames + 'px';
    } else {
      this.style.automaticHeight = automaticContent - automaticHeader - automaticHead - 30 + 'px';
    }
  }
  scroll() {
    this.rightAutomatic.addScrollEventListener(function (event: any) {
      automaticThis.headerAutomatic._scrollContent.nativeElement.scrollLeft = event.target.scrollLeft;
      automaticThis.leftAutomatic._scrollContent.nativeElement.scrollTop = event.target.scrollTop;
    });
  }
  getAutomaticData() {
    let startDate;
    let endDate;
    if (this.arrDate == "minute") {
      this.dataType = '1';
      startDate = this.minuteDate.startDate.replace("T", " ").replace("Z", "").substring(0, 16) + ':00';
      endDate = this.minuteDate.endDate.replace("T", " ").replace("Z", "").substring(0, 16) + ':00';
    } else if (this.arrDate == "hour") {
      startDate = this.minuteDate.startDate.replace("T", " ").replace("Z", "").substring(0, 13) + ':00:00';
      endDate = this.minuteDate.endDate.replace("T", " ").replace("Z", "").substring(0, 13) + ':00:00';
      this.dataType = '2';
    } else if (this.arrDate == "day") {
      startDate = this.minuteDate.startDate.replace("T", " ").replace("Z", "").substring(0, 10) + ' 00:00:00';
      endDate = this.minuteDate.endDate.replace("T", " ").replace("Z", "").substring(0, 10) + ' 00:00:00';
      this.dataType = '3';
    }
    let param: any = {};
    param.siteId = this.treeId;
    param.startTime = startDate;
    param.endTime = endDate;
    param.pageNum = this.pageNum;
    param.dataType = this.dataType;
    param.pageSize = this.pageSize;
    param.userId = AppServiceProvider.getInstance().userInfo.userId;
    param.code = AppServiceProvider.getInstance().userInfo.code;
    this.net.httpPost(
      AppGlobal.API.getWaterData,
      param, res => {
        if (res) {
          for (let AutomaticData of res) {
            this.AutomaticData.push(AutomaticData);
          }
        }
        console.log(this.AutomaticData);
        setTimeout(() => {
          this.scrollStyle();
        }, 500)
      }, error => {
        this.toast(error);
      }
    )
  }
  loadMore() {
    this.pageNum = this.AutomaticData.length;
    this.getAutomaticData();
  }
}
class minuteDate {
  maxDate: string = "";
  startDate: string = "";
  endDate: string = "";
}
class style {
  automaticStationName: string = '';
  automaticEquipmentName: string = '';
  automaticDateTime: string = '';
  automaticSw: string = '';
  automaticPh: string = '';
  automaticZd: string = '';
  automaticDdl: string = '';
  automaticPb: string = '';
  automaticRjy: string = '';
  automaticShen: string = '';
  automaticNh3N: string = '';
  automaticComprehensiveToxicity: string = '';
  automaticTp: string = '';
  automaticChromium: string = '';
  automaticWaterSort: string = '';
  automaticHeight: string = '';
}


